:root {
  --blue-color: #00acdc;
}

body {
  text-transform: uppercase;
  text-align: left;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

header {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 1200px;
  padding: 0 50px;
  display: flex;
  position: relative;
}

.nav_list {
  justify-content: space-around;
  align-items: stretch;
  width: 100%;
  height: 60px;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav_item {
  margin: 0 10px;
  position: relative;
}

.nav_item a {
  color: #000;
  align-items: center;
  height: 100%;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav_item a.is-active {
  color: var(--blue-color);
}

.nav_item a.is-active:after {
  content: "";
  background-color: var(--blue-color);
  box-sizing: border-box;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav_item a:hover {
  color: var(--blue-color);
  border-bottom: 2px solid var(--blue-color);
}

[data-qa="hover"] {
  color: var(--blue-color);
}

.nav_item:last-child {
  margin: 0 0 0 10px;
}

.nav_item:first-child {
  margin: 0 10px 0 0;
}

.img {
  width: 40px;
  height: 40px;
}
